import { calculateTotalPrice } from "./calculateTotalPrice";

export const formOrder = (cart) => {
  if (!cart || !cart.length) return "<b> </b>";

  let message = "<b> :</b>\n\n";

  cart.forEach((item, index) => {
    const { balloon, quantity, inscription, note } = item;

    const title = balloon?.title || "   ";
    const pricePerUnit = balloon?.price
      ? `${balloon.price.toFixed(2)} `
      : "  ";

    const inscriptionCost = inscription ? 100 : 0;
    const totalPrice = (balloon?.price || 0) * quantity + inscriptionCost;

    message += `<b>${index + 1}.  :</b> ${title}\n`;
    message += ` <i>:</i> ${quantity}\n`;
    message += ` <i>  :</i> ${pricePerUnit}\n`;

    if (inscription) {
      message += ` <i>:</i> "${inscription}" (+100 )\n`;
    }
    if (note) {
      message += ` <i> :</i> "${note}"\n`;
    }

    message += ` <i> :</i> ${totalPrice} \n\n`;
  });

  const totalSum = calculateTotalPrice(cart);

  message += `<b>    :</b> ${totalSum} `;

  console.log(message);
  //   return message;
};
